@use "sass:color";

$primary-color: #c4ceff;
$secondary-color: #94a9fa;
$alt-color: color.adjust($primary-color, $lightness: -20%);
$text-color: #1d1d1b;
// $secondary-color: color.adjust($primary-color, $lightness: 65%);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    position: relative;
    font-family: system-ui;
    scroll-behavior: smooth;
}

body{
    padding: 0 20px;
}

#root{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;
}

.banner{
    width: 96%;
    max-width: 1200px;
    border-radius: 8px;
    border: 2px solid $primary-color;
}

h1, h2, h3, h4, h5, h6{
    color: $alt-color;
    text-align: center;
}

p{
    font-size: 18px;
}

nav{
    width: 96%;
    max-width: 1200px;
    height: 100px;
    background-color: $primary-color;
    border: 2px solid $secondary-color;
    margin-top: 20px;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 24px;

    img{
        width: 64px;

        &.atauni-logo{
            width: 80px;
        }
    }

    ul{
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 60px;

        li{

            a{
                text-decoration: none;
                color: $text-color;
                font-weight: bold;

                &:hover{
                    text-shadow: 0px 4px 4px $text-color;
                }
            }
        }
    }
}

section {
    width: 96%;
    max-width: 1200px !important;
    padding: 32px;
    gap: 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    margin-top: 20px;
    position: relative;

    h2{
        display: flex;
        justify-content: center;
        align-items: center;
        &::before {
            content: "";
            position: relative;
            display: inline-block;
            left: -20px;
            width: 60px;
            height: 3px;
            background-color: $alt-color;
        }

        &::after {
            content: "";
            position: relative;
            display: inline-block;
            right: -20px;
            width: 60px;
            height: 3px;
            background-color: $alt-color;
        }
    }

    // &:nth-child(2n+1){
    //     background-color: $primary-color;
    //     h2{
    //         color: $text-color;
    //         &::before {
    //             background-color: $text-color;
    //         }
    
    //         &::after {
    //             background-color: $text-color;
    //         }
    //     }
    //     p{
    //         color: $text-color;
    //     }
    // }

    &#intro, 
    &#about{
        background-color: $primary-color;
        h2{
            color: $text-color;
            &::before {
                background-color: $text-color;
            }
    
            &::after {
                background-color: $text-color;
            }
        }
        p{
            color: $text-color;
        }
        p{
            text-align: justify;
            width: 100%;
            a{
                color: $alt-color;
            }
        }

        p.attention{
            text-align: center;
            margin-top: 32px;
            color: #d41522;
        }

        .titles{
            margin: 32px 0;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            gap: 24px;

            .title-card{
                width: 240px;
                height: 120px;
                border: 1px solid #1d1d1b;
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                background-color: #94a9fa;
                color: #1d1d1b;
                font-weight: bold;
                border-radius: 20px;
                transition: transform .25s;
            
                &:hover{
                    transform: translateY(-8px);
                }
            }
        }

        ul{
            display: flex;
            flex-direction: column;
            gap: 16px;
            align-self: flex-start;
            color: $text-color;
            list-style: none;
            font-size: 18px;
            li{
                display: flex;
                justify-content: flex-start;
                align-items: center;
                gap: 20px;
                &:hover{
                    svg{
                        transform: translateX(4px);
                        color: $alt-color;
                    }
                }
            }
        }
    }

    &#presentation{
        width: 100%;
        padding: 32px 0;

        h2{
            display: flex;
            justify-content: center;
            align-items: center;

            // .select-banner-or-program{
            //     display: inline-flex;
            //     border-radius: 12px;
            //     width: 360px;
            //     height: 60px;
            //     border: 2px solid $primary-color;
            //     button{
            //         cursor: pointer;
            //         height: 100%;
            //         width: 180px;
            //         border: none;
            //         outline: none;
            //         font-weight: bold;
            //         font-size: 20px;
            //         background-color: #f7f5ec;
            //         color: $primary-color;
            //         transition: background-color .5s, color .5s;

            //         &:nth-child(1){
            //             border-top-left-radius: 10px;
            //             border-bottom-left-radius: 10px;
            //         }

            //         &:nth-child(2){
            //             border-top-right-radius: 10px;
            //             border-bottom-right-radius: 10px;
            //         }

            //         &.active{
            //             background-color: $primary-color;
            //             color: #f7f5ec;
            //             text-shadow: 0 0 4px #f7f5ec;
            //         }
            //     }
            // }
        }
        

        // .present-buttons{
        //     width: 100%;
        //     display: flex;
        //     align-items: center;
        //     justify-content: space-between;

        //     button{
        //         width: 80px;
        //         height: 100px;
        //         display: flex;
        //         justify-content: center;
        //         align-items: center;
        //         border: none;
        //         outline: none;
        //         background-color: unset;
        //         svg{
        //             width: 80px;
        //             height: 100px;
        //             color: $primary-color;
        //         }
        //         span{
        //             position: absolute;
        //             font-weight: bold;
        //             color: #f7f5ec;
        //         }
    
        //         &.active{
        //             svg{
        //                 color: $primary-color;
        //             }
        //             span{
        //                 position: absolute;
        //                 font-weight: bold;
        //                 color: #f7f5ec;
        //             }
        //         }
    
        //         &:hover{
        //             cursor: pointer;
                    
        //         }
    
        //         &:nth-child(1):hover{
        //             svg{
        //                 filter: brightness(1.5) drop-shadow(0px 8px 5px $primary-color);
        //             }
        //             span{
        //                 text-shadow: 0px 1px 5px white;
        //             }
        //         }
    
        //         &:nth-child(2):hover{
        //             svg{
        //                 filter: brightness(1.5) drop-shadow(0px 8px 5px $primary-color);
        //             }
        //             span{
        //                 text-shadow: 0px 1px 5px white;
        //             }
        //         }
        //     }
        // }
        

        table{
            width: 100%;
            tr{
                height: 48px;
                // &.caption{
                //     background-color: $primary-color;
                //     color: #f7f5ec;
                // }
                &.sub-caption{
                    background-color: $primary-color;
                    color: $text-color;
                }

                th, td{
                    padding: 12px 20px;
                    border: 3px solid $primary-color;
                    // text-align: center;
                }

                .long-column, .long-row{
                    text-align: center;
                    background-color: $primary-color;
                }

                .long-row{
                    color: $primary-color;
                }

                td.lecture-name{
                    background-color: $secondary-color;
                    color: $text-color;
                }

                td.td-start{
                    align-content: flex-start;
                }

                .long-column{
                    width: 152px;
                    span{
                        display: inline-block;
                        margin-top: 8px;
                        font-weight: bold;
                        
                        &.lecture-no{
                            color: $primary-color;
                        }
                    }
                }
                &:last-child{
                    background-color: $primary-color;
                    color: $text-color;
                    font-weight: bold;
                    text-align: center;
                }
                
            }

            .lecture-name, .lecturer-name{
                font-weight: bold;
            }

            .lecturer-name{
                color: $alt-color;
            }

            p{
                display: flex;
                flex-direction: column;
                gap: 8px;
                text-align: justify;
                color: $text-color;
                strong, svg{
                    color: $alt-color;
                }
            }

            ul{
                list-style: none;
                display: flex;
                flex-direction: column;
                gap: 8px;

                strong, svg{
                    color: $alt-color;
                }

                li{
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                }

            }
        }

        img.banner{
            width: 100%;
        }
    }

    &#instructors {

        &.committee{
            margin-top: 0;
            padding-top: 0;
        }
        .cards {
            display: flex;
            gap: 32px;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            .instructor-card {
                padding: 30px 0 40px;
                margin-bottom: 30px;
                background-color: $primary-color;
                // background-color: #f7f5ec;
                text-align: center;
                overflow: hidden;
                position: relative;
                width: 240px;
                border-radius: 12px;
    
                .picture {
                    display: inline-block;
                    height: 130px;
                    width: 130px;
                    margin-bottom: 50px;
                    z-index: 1;
                    position: relative;
    
                    &::before {
                        content: "";
                        width: 100%;
                        height: 0;
                        border-radius: 50%;
                        // background-color: $primary-color;
                        background-color: $alt-color;
                        position: absolute;
                        bottom: 135%;
                        right: 0;
                        left: 0;
                        opacity: 0.9;
                        transform: scale(3);
                        transition: all 0.3s linear 0s;
                    }
    
                    &::after {
                        content: "";
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                        // background-color: $primary-color;
                        position: absolute;
                        top: 0;
                        left: 0;
                        z-index: -1;
                    }
    
                    img {
                        width: 100%;
                        height: auto;
                        border-radius: 50%;
                        transform: scale(1);
                        transition: all 0.9s ease 0s;
                    }
                }
    
                .title {
                    display: block;
                    font-size: 16px;
                    color: $text-color;
                    text-transform: capitalize;
                }
    
                .about {
                    width: 100%;
                    height: 32px;
                    padding: 0;
                    margin: 0;
                    // background-color: $primary-color;
                    background-color: $alt-color;
                    position: absolute;
                    bottom: -100px;
                    left: 0;
                    transition: all 0.5s ease 0s;
                    a{
                        padding: 8px 0;
                        font-size: 12px;
                        color: white;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 100%;
                        height: 100%;
                        transition: all 0.3s ease 0s;
                        text-decoration: none;
                        font-weight: bold;
                    }
                }
    
                &:hover{
                    .picture{
                        img{
                            box-shadow: 0 0 1px 14px #f7f5ec;
                            border: 1px solid $alt-color;
                            transform: scale(0.7);
                        }
    
                        &::before{
                            height: 100%;
                        }
    
                        &::after{
                            background-color: $secondary-color;
                        }
                    }
    
                    .about {
                        bottom: 0;
    
                        &:hover{
                            border-top-left-radius: 12px;
                            border-top-right-radius: 12px;
                            a{
                                color: #f7f5ec;
                                text-shadow: 0 0 5px white;
                            }
                        }
                    }
                }
            }
        }
    }
    
}

.fixed-up-button{
    width: 50px;
    height: 50px;
    position: fixed;
    bottom: 50px;
    right: 50px;
}

footer{
    width: 96%;
    max-width: 1200px;
    // height: 100px;
    background-color: $primary-color;
    border: 2px solid $secondary-color;
    margin-bottom: 20px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    gap: 40px;

    div#footer-content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;

        div#footer-inner-content{
            width: 100%;
            display: flex;
            justify-content: space-evenly;
            h5{
                color: $alt-color;
                border-bottom: 2px solid $alt-color;
                padding: 0 0 5px;
                font-size: 16px;
                margin-bottom: 12px;
                // text-align: left;
            }
            
            #footer-left, #footer-right{
                width: 300px;

                ul{
                    list-style: none;
                    display: flex;
                    flex-direction: column;
                    gap: 12px;
                    li{
                        display: flex;
                        justify-content: flex-start;
                        align-items: flex-start;
                        font-size: 16px;
                        color: $alt-color;
                        svg{
                            font-size: 20px;
                        }

                        svg, .title-span{
                            font-weight: bold;
                        }
                        .title-span{
                            margin-left: 4px;
                            text-wrap-mode: nowrap;
                        }
                        a, .content-span{
                            margin-left: 8px;
                            color: $text-color;
                            text-align: justify;
                            overflow-wrap: anywhere;
                        }
                        
                    }
                }
            }

            #footer-left{
                ul{
                    align-items: center;
                    a{
                        color: $alt-color !important;
                        text-decoration: none;
                        &:hover{
                            text-shadow: 0 0 5px $alt-color;
                            text-decoration: underline;
                            font-weight: bold;
                        }
                    }
                }
            }

        }
    }

    img{
        width: 64px;

        &.atauni-logo{
            width: 80px;
        }
    }
}

