                        border-top-left-radius: 8px;
                        border-top-right-radius: 8px;
                        a{
                            font-size: 10px;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 400px) {
    nav{
        max-width: 300px;
        padding: 16px 16px;
        height: 100%;
        img{
            width: 44px;
    
