                width: 44px;
            }
        }
    
        ul{
            flex-direction: column;
            gap: 8px;
            li{
                a{
                    font-size: 12px;
                }
            }
        }
    }

    .banner{
        max-width: 300px;
    }

    section{
        max-width: 300px !important;
        gap: 12px;
        padding: 20px;
        margin-top: 8px;
        h2{
            font-size: 12px;
        }
        p{
            font-size: 10px;
        }

        &#intro{
            .titles{
                gap: 12px;
                .title-card{
                    font-size: 10px;
